import React, { useState, useEffect } from "react";
import MessageService from "../services/MessageService";
import avatar_default from '../image/avatar_user.jpg';
import '../css/ListFriend_RequestTab.css';
import { useAuth } from "../context/AuthContext";
import { useWebSocket } from "../context/WebSocket";
import UserService from "../services/UserService";

const FriendRequestsTab = ({ userId, friendRequests, onSelectChat }) => {
    const { MyUser, updateUserInfo } = useAuth();
    const [receivedRequests, setReceivedRequests] = useState([]);
    const [sentRequests, setSentRequests] = useState([]);
    const [loading, setLoading] = useState(true);
    const [error, setError] = useState(null);
    // const { sendMessage, onMessage } = useWebSocket();
    const [userInfoMap, setUserInfoMap] = useState({});
    const [data, setData] = useState([]);
    const [lastFetched, setLastFetched] = useState(null); // Lu thi gian ly d liu

    const { sendMessage, onMessage } = useWebSocket();


    // Lng nghe tn hiu thu hi/t chi li mi kt bn
    useEffect(() => {
        const unsubscribe = onMessage((message) => {
            console.log("Received message:", message);

            // Trng hp c li mi kt bn vi y  thng tin
            if (
                message.status === "Ch ng " &&
                message.senderID &&
                message.receiverID &&
                message.id &&
                message.content
            ) {
                setReceivedRequests((prev) => {
                    const exists = prev.some(
                        (req) =>
                            req.senderID === message.senderID &&
                            req.receiverID === message.receiverID
                    );
                    if (!exists) {
                        return [...prev, message];
                    }
                    return prev;
                });
            }

            if (message.type === "REVOKE_INVITATION") {
                // X l khi nhn thng bo xa li mi
                setReceivedRequests((prev) =>
                    prev.filter(
                        (request) =>
                            request.senderID !== message.sender ||
                            request.receiverID !== message.receiver
                    )
                );
                setSentRequests((prev) =>
                    prev.filter(
                        (request) =>
                            request.senderID !== message.sender ||
                            request.receiverID !== message.receiver
                    )
                );
            }

            if (message.type === "REFUSE_INVITATION" || message.type === "SUBMIT_FRIEND_REQUEST") {
                setReceivedRequests((prev) =>
                    prev.filter(
                        (request) =>
                            request.senderID !== message.receiver &&
                            request.receiverID !== message.sender
                    )
                );

                setSentRequests((prev) =>
                    prev.filter(
                        (request) =>
                            request.senderID !== message.receiver ||
                            request.receiverID !== message.sender
                    )
                );
            }
        });

        return () => unsubscribe();
    }, [onMessage]);

    // Hm chuyn i timestamp thnh ngy thng, ch n pht
    const formatDate = (timestampArray) => {
        if (Array.isArray(timestampArray)) {
            let date = new Date(
                timestampArray[0],
                timestampArray[1] - 1,
                timestampArray[2],
                timestampArray[3],
                timestampArray[4],
                timestampArray[5],
                timestampArray[6] / 1000000
            );

            date.setHours(date.getHours() + 7);

            return date.toLocaleString('vi-VN', {
                day: '2-digit',
                month: '2-digit',
                year: 'numeric',
                hour: '2-digit',
                minute: '2-digit',
            });
        } else {
            // Nu l chui ISO, tr v trc tip
            let date = new Date(timestampArray);
            date.setHours(date.getHours() + 7);
            return date.toLocaleString('vi-VN', {
                day: '2-digit',
                month: '2-digit',
                year: 'numeric',
                hour: '2-digit',
                minute: '2-digit',
            });
        }
    };


    const fetchRequests = () => {
        setLoading(true);

        // Ly li mi kt bn  nhn
        MessageService.get(`/invitations/received/${userId}`)
            .then((data) => {
                setReceivedRequests(data);
            })
            .catch((error) => {
                setError('Li khi ly d liu li mi  nhn');
            });

        // Ly li mi kt bn  gi
        MessageService.get(`/invitations/sent/${userId}`)
            .then((data) => {
                setSentRequests(data);
            })
            .catch((error) => {
                setError('Li khi ly li mi  gi');
            })
            .finally(() => {
                setLoading(false);
            });
    };

    useEffect(() => {
        fetchRequests();
    }, [friendRequests]);

    // Hm x l t chi li mi kt bn
    const handleDeleteInvitation_refuse = (senderID, receiverID) => {
        MessageService.deleteInvitation_refuse(senderID, receiverID)
            .then(() => {
                fetchRequests();  // Cp nht li danh sch sau khi xa
                alert("Li mi  b t chi.");

                // sendMessage({
                //     senderID: receiverID,
                //     receiverID: senderID,
                // });
            })
            .catch((error) => {
                console.error("Li khi xa li mi:", error);
                alert(" xy ra li khi xa li mi.");
            });
    };

    // Hm x l thu hi li mi kt bn
    const handleDeleteInvitation_revoke = (senderID, receiverID) => {
        MessageService.deleteInvitation(senderID, receiverID)
            .then(() => {
                fetchRequests();  // Cp nht li danh sch sau khi xa
                alert("Li mi  b thu hi.");
                // sendMessage({
                //     senderID,
                //     receiverID,
                // });
            })
            .catch((error) => {
                console.error("Li khi xa li mi:", error);
                alert(" xy ra li khi xa li mi.");
            });
    };

    // Hm x l chp nhn li mi kt bn
    const handleAcceptRequest = (senderId, receiverId) => {
        const friendIds = Array.isArray(MyUser?.my_user?.friendIds) ? MyUser.my_user.friendIds : [];
        MessageService.post(`/acceptFriendRequest/${senderId}/${receiverId}`)
            .then((response) => {
                alert(response);
                fetchRequests();

                const updatedUserData_receiver = {
                    ...MyUser,
                    my_user: {
                        ...MyUser.my_user,
                        friendIds: [...friendIds, senderId],
                    },
                };
                updateUserInfo(updatedUserData_receiver);

                const message = {
                    id: new Date().getTime().toString(),
                    senderID: receiverId,
                    receiverID: senderId,
                    content: "Ti  chp nhn li mi kt bn ca bn.",
                    sendDate: new Date().toISOString(),
                    isRead: true,
                };

                //Gi thng bo qua WebSocket n bn A v vic ng  kt bn
                sendMessage(message);


                const sender = userInfoMap[senderId]
                onSelectChat(sender); // Chuyn n cuc tr chuyn vi ngi bn mi

            })
            .catch((error) => {
                console.error('Li khi ng  kt bn:', error.response || error);
                alert(`C li xy ra khi ng  kt bn: ${error.response ? error.response.data : error.message}`);
            });
    };


    const getUserInfoById = async (userId) => {
        setLoading(true);
        try {
            const user = await UserService.getUserById(userId);
            setUserInfoMap(prev => ({ ...prev, [userId]: user })); // Cp nht thng tin ngi dng vo map
        } catch (error) {
            console.error("Error fetching user info:", error);
            setUserInfoMap(prev => ({ ...prev, [userId]: { name: "Ngi dng khng xc nh", avatar: avatar_default } }));
        } finally {
            setLoading(false);
        }
    };

    useEffect(() => {
        sentRequests.forEach(request => {
            if (!userInfoMap[request.receiverID]) {
                getUserInfoById(request.receiverID);
            }
        });
    }, [sentRequests, userInfoMap]);

    // console.log("sentRequests Requests:", sentRequests);

    useEffect(() => {
        receivedRequests.forEach(request => {
            if (!userInfoMap[request.senderID]) {
                getUserInfoById(request.senderID);
            }
        });
    }, [receivedRequests, userInfoMap]);

    // Kim tra nu ang ti d liu hoc c li
    if (loading) {
        return <p>ang ti d liu...</p>;
    }

    if (error) {
        return <p>{error}</p>;
    }

    return (
        <div className="friend-requests-container">
            {/* Danh sch li mi  nhn */}
            <h5 className="friend-request-header">Li mi kt bn  nhn</h5>
            <div className="request-list">
                {receivedRequests.length > 0 ? (
                    receivedRequests.map((request) => {
                        const sender = userInfoMap[request.senderID] || { name: "ang ti...", avatar: avatar_default }; // Ly thng tin ngi gi t map
                        return (
                            <div key={request.id} className="request-item">
                                <div>
                                    <img src={sender.avatar || avatar_default} alt="Avatar" />
                                    <span>T: {sender.name}</span>
                                    <span>{formatDate(request.sendDate)}</span>
                                </div>
                                <div className="request-content">
                                    <span>{request.content}</span>
                                </div>
                                <div className="list-request-buttons-recieve">
                                    <button className="request-button-ok" onClick={() => handleAcceptRequest(request.senderID, userId)}>ng </button>
                                    <button className="request-button" onClick={() => handleDeleteInvitation_refuse(request.senderID, request.receiverID)}>T chi</button>
                                </div>
                            </div>
                        );
                    })
                ) : (
                    <p>Khng c li mi kt bn mi.</p>
                )}
            </div>

            {/* Danh sch li mi  gi */}
            <h5 className="friend-request-header" style={{ marginTop: "10px" }}>Li mi kt bn  gi</h5>
            <div className="request-list">
                {sentRequests.length > 0 ? (
                    sentRequests.map((sentRequest) => {
                        const receiver = userInfoMap[sentRequest.receiverID] || { name: "ang ti...", avatar: avatar_default };
                        return (
                            <div key={sentRequest.id} className="request-item">
                                <div>
                                    <img src={receiver.avatar || avatar_default} alt="Avatar" />
                                    <span>{receiver.name}</span>
                                    <span>{formatDate(sentRequest.sendDate)}</span>
                                </div>
                                <div className="request-content" style={{ backgroundColor: "#f0f0f0", padding: "5px", borderRadius: "5px" }}>
                                    <span>{sentRequest.content}</span>
                                </div>
                                <div className="list-request-buttons-sent">
                                    <button className="request-button" onClick={() => handleDeleteInvitation_revoke(sentRequest.senderID, sentRequest.receiverID)}>Thu hi li mi</button>
                                </div>
                            </div>
                        );
                    })
                ) : (
                    <p>Khng c li mi kt bn  gi.</p>
                )}
            </div>
        </div>
    );
};

export default FriendRequestsTab;
