import React, { useEffect, useState } from 'react';
import ApiService from '../services/AuthService';
import { useNavigate } from 'react-router-dom';
import { formatPhoneNumber } from '../utils/formatPhoneNumber'; // Import hm t file utils
import "../css/LoginPage.css";
import showToast from '../utils/AppUtils';

const ForgotPasswordPage = () => {
    const navigate = useNavigate();
    const [phoneNumber, setPhoneNumber] = useState('');
    const [otp, setOtp] = useState('');
    const [newPassword, setNewPassword] = useState('');
    const [confirmNewPassword, setConfirmNewPassword] = useState('');
    const [errorMessage, setErrorMessage] = useState('');
    const [step, setStep] = useState(1);
    const [isProcessing, setIsProcessing] = useState(false);

    // Gi m OTP
    const handleSendOtp = async () => {
        setIsProcessing(true);
        setErrorMessage('');

        // Kim tra v nh dng s in thoi bng hm formatPhoneNumber  import
        const formattedPhone = formatPhoneNumber(phoneNumber);
        setPhoneNumber(formattedPhone);
        if (!formattedPhone) {
            setErrorMessage('S in thoi khng hp l!');
            setIsProcessing(false);
            return;
        }

        try {
            const response = await ApiService.post('/forgot-password/send-otp', { phoneNumber: formattedPhone });
            setStep(2);  // Chuyn sang bc xc minh OTP
            setIsProcessing(false);
        } catch (error) {
            setErrorMessage('Li gi m OTP, vui lng th li!');
            setIsProcessing(false);
        }
    };

    // Xc minh OTP v chuyn sang nhp mt khu mi
    const handleVerifyOtp = async () => {
        setIsProcessing(true);
        setErrorMessage('');
        try {
            const response = await ApiService.post('/forgot-password/verify-otp', { phoneNumber, otp });
            setStep(3);  // Chuyn sang bc nhp mt khu mi
            setIsProcessing(false);
        } catch (error) {
            setErrorMessage('M OTP khng ng hoc ht hn!!');
            setIsProcessing(false);
        }
    };

    const validatePassword = (pass) => {
        const passwordRegex = /^(?=.*[a-z])(?=.*[A-Z])(?=.*\d)(?=.*[@$!%*?&])[A-Za-z\d@$!%*?&]{8,}$/;
        return passwordRegex.test(pass);
    };

    // t mt khu mi
    const handleResetPassword = async () => {
        setIsProcessing(true);
        setErrorMessage('');

        // Kim tra xem mt khu mi v mt khu nhp li c trng khp khng
        if (newPassword !== confirmNewPassword) {
            setErrorMessage('Mt khu mi v mt khu nhp li khng khp!');
            setIsProcessing(false);
            return;
        }

        // Kim tra nh dng mt khu mi
        if (!validatePassword(newPassword)) {
            setErrorMessage('Mt khu mi phi bao gm ch hoa, ch thng, s v k t c bit!');
            setIsProcessing(false);
            return;
        }

        try {
            const response = await ApiService.post('/forgot-password/reset-password', { phoneNumber, newPassword });
            setIsProcessing(false);
            showToast('t li mt khu thnh cng!', 'success');
            navigate('/login');  // iu hng ngi dng n trang ng nhp
        } catch (error) {
            setErrorMessage('C li khi thay i mt khu!');
            setIsProcessing(false);
        }
    };

    return (
        <div className="flex justify-center items-center min-h-screen bg-gray-100 p-4 m-4" style={{ textAlign: 'center', alignItems: 'center', display: 'flex', flexDirection: 'column' }}>
            <div className="w-full max-w-md bg-white p-8 rounded-lg shadow-lg" style={{ width: '400px', borderRadius: '10px' }}>
                <h2 className="text-2xl font-semibold text-center mb-6 pt-4">Qun Mt Khu</h2>

                {step === 1 && (
                    <div className="m-4">
                        <div className="mb-4">
                            {errorMessage && <div className="alert alert-danger">{errorMessage}</div>}
                            <input
                                type="text"
                                placeholder=" Nhp s in thoi"
                                value={phoneNumber}
                                onChange={(e) => setPhoneNumber(e.target.value)}
                                className="w-full p-3 border border-gray-300 rounded-md focus:ring-2 focus:ring-blue-500"
                                required
                            />
                        </div>
                        <button
                            onClick={handleSendOtp}
                            className="w-full bg-blue-500 text-white py-2 rounded-md hover:bg-blue-600"
                            style={{ width: '150px', padding: '10px', cursor: 'pointer' }}
                            disabled={isProcessing}
                        >
                            {isProcessing ? 'ang x l...' : 'Gi M OTP'}
                        </button>
                    </div>
                )}

                {step === 2 && (
                    <div className="m-4">
                        <div className="">
                            {errorMessage && <div className="alert alert-danger">{errorMessage}</div>}
                            <input
                                type="text"
                                placeholder=" Nhp m OTP"
                                value={otp}
                                onChange={(e) => setOtp(e.target.value)}
                                className="w-full p-3 mb-4 border border-gray-300 rounded-md focus:ring-2 focus:ring-blue-500"
                                required
                            />
                        </div>
                        <button
                            onClick={handleVerifyOtp}
                            className="w-full bg-blue-500 text-white py-2 rounded-md hover:bg-blue-600"
                            style={{ width: '150px', padding: '10px', cursor: 'pointer' }}
                            disabled={isProcessing}
                        >
                            {isProcessing ? 'ang x l...' : 'Xc Minh OTP'}
                        </button>
                    </div>
                )}

                {step === 3 && (
                    <div className="m-4">
                        <div>
                            {errorMessage && <div className="alert alert-danger">{errorMessage}</div>}
                            <input
                                type="password"
                                placeholder=" Nhp mt khu mi"
                                value={newPassword}
                                onChange={(e) => setNewPassword(e.target.value)}
                                className="w-full p-3 mb-4 border border-gray-300 rounded-md focus:ring-2 focus:ring-blue-500"
                                required
                            />
                        </div>
                        <div>
                            <input
                                type="password"
                                placeholder=" Nhp li mt khu mi"
                                value={confirmNewPassword}
                                onChange={(e) => setConfirmNewPassword(e.target.value)}
                                className="w-full p-3 mb-4 border border-gray-300 rounded-md focus:ring-2 focus:ring-blue-500"
                                required
                            />
                        </div>
                        <button
                            onClick={handleResetPassword}
                            className="w-auto bg-blue-500 text-white py-2 rounded-md hover:bg-blue-600"
                            style={{ width: '150px', padding: '10px', cursor: 'pointer' }}
                            disabled={isProcessing}
                        >
                            {isProcessing ? 'ang x l...' : 'Xc Nhn Mt Khu Mi'}
                        </button>
                    </div>
                )}
            </div>
        </div>
    );
};

export default ForgotPasswordPage;