import axios from 'axios';
import showToast from '../utils/AppUtils';

const API_BASE_URL = 'https://deploybe-cnm-production.up.railway.app/s3';

const S3Service = {
    /**
     * Upload avatar ln S3
     * @param {File} file - File nh cn upload
     * @returns {Promise<string>} - URL ca avatar sau khi upload
     */
    uploadAvatar: async (file, userId) => {
        const formData = new FormData();
        formData.append("file", file);
        formData.append("userId", userId);

        try {
            const response = await axios.post(`${API_BASE_URL}/avatar`, formData, {
                headers: { "Content-Type": "multipart/form-data" },
            });

            if (response.data.url) {
                return response.data.url; // Tr v URL nh t server
            } else {
                throw new Error("Khng nhn c URL t server");
            }
        } catch (error) {
            console.error("Li upload:", error);

            // Kim tra li 400 (vt qu kch thc)
            if (error.response && error.response.status === 400) {
                showToast("Kch thc file vt qu 20MB.", "error")
            } else {
                // Thng bo li chung nu khng phi li 400
                showToast("Kch thc file vt qu 20MB.", "error")
            }

            throw error.response ? error.response.data : error;
        }
    },

    uploadImage: async (file) => {
        const formData = new FormData();
        formData.append("file", file);

        try {
            const response = await axios.post(`${API_BASE_URL}/image`, formData, {
                headers: { "Content-Type": "multipart/form-data" },
            });
            return response.data.url; // Tr v URL nh mi
        } catch (error) {
            // Kim tra li 400 (vt qu kch thc)
            if (error.response && error.response.status === 400) {
                showToast("Kch thc file vt qu 20MB.", "error")
            } else {
                // Thng bo li chung nu khng phi li 400
                showToast("Kch thc file vt qu 20MB.", "error")
            }
            throw error.response ? error.response.data : error;
        }
    },

    uploadFile: async (file) => {
        const formData = new FormData();
        formData.append("file", file);

        try {
            const response = await axios.post(`${API_BASE_URL}/file`, formData, {
                headers: { "Content-Type": "multipart/form-data" },
            });
            return response.data.url; // Tr v URL file mi
        } catch (error) {
            // Kim tra li 400 (vt qu kch thc)
            if (error.response && error.response.status === 400) {
                showToast("Kch thc file vt qu 20MB.", "error")
            } else {
                // Thng bo li chung nu khng phi li 400
                showToast("Kch thc file vt qu 20MB.", "error")
            }
            throw error.response ? error.response.data : error;
        }
    },
};

export default S3Service;
