export const formatPhoneNumber = (phone) => {
    if (!phone) return null;

    // Loi b khong trng
    const cleaned = phone.replace(/\s+/g, '');

    // Kim tra nh dng: ch cha s v ty chn du +  u
    if (!/^(\+?\d+)$/.test(cleaned)) {
        return null;
    }

    // Nu  c +84 th kim tra  di (12 k t: +84 v 9 s)
    if (cleaned.startsWith('+84')) {
        return cleaned.length === 12 ? cleaned : null;
    }

    // Nu bt u bng 0 v c 10 ch s, chuyn thnh nh dng quc t +84
    if (cleaned.startsWith('0') && cleaned.length === 10) {
        return '+84' + cleaned.slice(1);
    }

    // Nu khng tha mn cc iu kin trn, tr v null
    return null;
};  