import React, { useState } from "react";
import { useNavigate } from "react-router-dom";
import AuthService from "../services/AuthService";
import { formatPhoneNumber } from "../utils/formatPhoneNumber"; // Import hm formatPhoneNumber
import '../css/RegisterPage.css';
import showToast from "../utils/AppUtils";

const RegistePage = () => {
    const [phoneNumber, setPhoneNumber] = useState("");
    const [password, setPassword] = useState("");
    const [confirmPassword, setConfirmPassword] = useState("");
    const [verificationCode, setVerificationCode] = useState("");
    const [errorMessage, setErrorMessage] = useState("");
    const [successMessage, setSuccessMessage] = useState("");
    const [isOtpSent, setIsOtpSent] = useState(false);
    const [name, setName] = useState("");
    const [dob, setDob] = useState("");
    const navigate = useNavigate();

    const validatePassword = (pass) => {
        const passwordRegex = /^(?=.*[a-z])(?=.*[A-Z])(?=.*\d)(?=.*[@$!%*?&])[A-Za-z\d@$!%*?&]{8,}$/;
        return passwordRegex.test(pass);
    };

    // Kim tra ngi dng c  12 tui
    const validateAge = (dobString) => {
        const dobDate = new Date(dobString);
        const today = new Date();
        let age = today.getFullYear() - dobDate.getFullYear();
        if (
            today.getMonth() < dobDate.getMonth() ||
            (today.getMonth() === dobDate.getMonth() && today.getDate() < dobDate.getDate())
        ) {
            age--;
        }
        return age;
    };

    const handleSendOtp = async () => {
        setErrorMessage("");
        setSuccessMessage("");

        if (!phoneNumber || !password || !confirmPassword || !name || !dob) {
            setErrorMessage("S in thoi, mt khu, tn v ngy sinh l bt buc.");
            return;
        }

        if (validateAge(dob) < 12) {
            setErrorMessage("Bn phi t nht 12 tui  ng k.");
            return;
        }

        // nh dng s in thoi trc khi kim tra
        const formattedPhone = formatPhoneNumber(phoneNumber);
        if (!formattedPhone) {
            setErrorMessage("S in thoi khng ng nh dng.");
            return;
        }
        // Cp nht li state vi s in thoi  c nh dng
        setPhoneNumber(formattedPhone);

        if (!validatePassword(password)) {
            setErrorMessage("Mt khu phi c  nht 8 k t, ch hoa, ch thng, s v k t c bit.");
            return;
        }

        if (password !== confirmPassword) {
            setErrorMessage("Mt khu xc nhn khng khp.");
            return;
        }

        try {
            await AuthService.post("/send-otp", { phoneNumber: formattedPhone, password });
            setSuccessMessage("OTP  c gi thnh cng. Vui lng kim tra in thoi ca bn.");
            setIsOtpSent(true);
        } catch (error) {
            setErrorMessage(error.response?.data || "Li khi gi OTP.");
        }
    };

    const handleVerifyOtp = async () => {
        setErrorMessage("");
        setSuccessMessage("");

        if (!verificationCode) {
            setErrorMessage("OTP l bt buc.");
            return;
        }

        const user = {
            id: new Date().getTime().toString(),
            dob: dob,
            name: name,
            phoneNumber: phoneNumber,
        };

        try {
            const requestData = {
                phoneNumber: phoneNumber,
                verificationCode: verificationCode,
                user: user,
            };

            await AuthService.post("/verify-phone-and-create-user", requestData);
            setSuccessMessage("To ngi dng thnh cng. Bn c th ng nhp ngay.");
            showToast("To ngi dng thnh cng. Bn c th ng nhp ngay.", "success");
            setTimeout(() => {
                navigate("/");
            }, 2000);
        } catch (error) {
            if (error.response?.data) {
                setErrorMessage(error.response?.data);
            } else {
                setErrorMessage("M OTP khng ng hoc ht hn.");
            }
        }
    };

    return (
        <div className="d-flex justify-content-center align-items-center flex-column vh-100">
            <div className="text-center mb-4">
                <h1 className="text-primary fw-bold">Zolo</h1>
                <p>
                    ng k ti khon Zolo <br />  kt ni vi ng dng Zolo Web
                </p>
            </div>
            <div className="card p-4" style={{ width: "500px", borderRadius: "20px" }}>
                <h1>{isOtpSent ? "Xc minh OTP" : "ng k Ti khon"}</h1>
                {errorMessage && <div className="error-message">{errorMessage}</div>}
                {successMessage && <div className="success-message">{successMessage}</div>}
                {!isOtpSent ? (
                    <div>
                        <div className="input-group-info">
                            <input
                                id="name"
                                type="text"
                                placeholder=" H v tn"
                                value={name}
                                onChange={(e) => setName(e.target.value)}
                                className="name"
                            />
                            <input
                                id="dob"
                                type="date"
                                value={dob}
                                onChange={(e) => setDob(e.target.value)}
                                className="dob"
                            />
                        </div>
                        <div className="input-group">
                            <input
                                id="phone"
                                type="text"
                                placeholder=" S in thoi"
                                value={phoneNumber}
                                onChange={(e) => setPhoneNumber(e.target.value)}
                            />
                        </div>
                        <div className="input-group">
                            <input
                                id="password"
                                type="password"
                                placeholder=" Mt khu"
                                value={password}
                                onChange={(e) => setPassword(e.target.value)}
                            />
                        </div>
                        <div className="input-group">
                            <input
                                id="confirm-password"
                                type="password"
                                placeholder=" Xc nhn mt khu"
                                value={confirmPassword}
                                onChange={(e) => setConfirmPassword(e.target.value)}
                            />
                        </div>
                        <button className="btn btn-primary w-100 mb-3" onClick={handleSendOtp}>
                            ng k
                        </button>
                    </div>
                ) : (
                    <div>
                        <label htmlFor="verificationCode"> Nhp OTP</label>
                        <input
                            id="verificationCode"
                            type="text"
                            placeholder="Nhp OTP"
                            value={verificationCode}
                            onChange={(e) => setVerificationCode(e.target.value)}
                        />
                        <button onClick={handleVerifyOtp}>Xc minh OTP</button>
                    </div>
                )}
                <hr />
                <div className="text-center">
                    <span> c ti khon? </span>
                    <a href="/" className="text-primary text-decoration-none fw-bold">
                        ng nhp
                    </a>
                </div>
            </div>
        </div>
    );
};

export default RegistePage;