import React, { useState } from "react";
import { useWebSocket } from "../context/WebSocket";
import { useAuth } from "../context/AuthContext";

const FriendInfoModal = ({
    user,
    avatar_default,
    MyUser,
    isUserInfoModalOpen,
    setIsUserInfoModalOpen,
    closeAllModal,
    handleUserInfoModalOpen,
    isFriendRequestSent,
    isFriendRequestModalOpen,
    messageContent,
    setMessageContent,
    sendFriendRequest,
    setIsFriendRequestModalOpen,
    openChat,
}) => {

    const friendIds = Array.isArray(MyUser?.my_user?.friendIds) ? MyUser.my_user.friendIds : [];

    const formatPhoneNumber = (phone) => {
        // Gi nguyn du +
        const countryCode = phone.startsWith('+') ? '+84' : '';
        const numberOnly = phone.replace(/^\+84/, ''); // loi b +84 nu c

        // Chia phn cn li thnh nhm 3-3-3
        const part1 = numberOnly.slice(0, 3);
        const part2 = numberOnly.slice(3, 6);
        const part3 = numberOnly.slice(6);

        return `${countryCode} ${part1} ${part2} ${part3}`.trim();
    };

    const formatDate = (isoDate) => {
        const date = new Date(isoDate);
        const year = date.getFullYear();
        const month = `${date.getMonth() + 1}`.padStart(2, '0'); // cng 1 v getMonth() t 0-11
        const day = `${date.getDate()}`.padStart(2, '0');
        return `${day}, thng ${month}, ${year}`;
    };

    const { updateUserInfo } = useAuth(); // S dng custom hook  ly hm updateUserInfo t context
    const [friendList, setFriendList] = useState([]);

    const updateFriendList = (friendId) => {
        const friendIds = Array.isArray(MyUser?.my_user?.friendIds) ? MyUser.my_user.friendIds : [];
        setFriendList((prevList) => {
            // Kim tra xem bn  c trong danh sch cha
            if (!prevList.includes(friendId)) {
                return [...prevList, friendId];  // Thm bn mi vo danh sch
            }
            return prevList;
        });

        // Cp nht li thng tin ngi dng nu cn
        const updatedUserData = {
            ...MyUser,
            my_user: {
                ...MyUser.my_user,
                friendIds: [...friendIds, friendId],
            },
        };
        updateUserInfo(updatedUserData);
    };

    useState(() => {
        updateFriendList(MyUser?.my_user?.friendIds); // Cp nht danh sch bn b khi modal m
    }, [MyUser?.my_user?.friendIds]);

    if (!isUserInfoModalOpen || !user) return null;
    const handleMessageClick = () => {
        openChat(user); // Gi hm m chat v truyn thng tin bn b
        setIsUserInfoModalOpen(false); // ng modal thng tin
    };

    return (
        <div className="overlay" onClick={closeAllModal}>
            <div className="modal-e" onClick={(e) => e.stopPropagation()}>
                <div className="modal-content user-info-modal">
                    <div className="modal-header">
                        <i className="fas fa-chevron-left" onClick={() => setIsUserInfoModalOpen(false)}></i>
                        <h2>Thng tin ti khon</h2>
                        <i className="fas fa-times" onClick={closeAllModal}></i>
                    </div>
                    <div className="modal-body">
                        <div>
                            <img src={user.avatar || avatar_default} alt="Avatar" />
                            <h3>{user.name}</h3>
                        </div>

                        <div className="action-buttons">
                            {!friendIds.includes(user.id) && (
                                <button onClick={handleUserInfoModalOpen}>
                                    {isFriendRequestSent ? 'Hy li mi' : 'Kt bn'}
                                </button>
                            )}
                            <button className="message-button" onClick={handleMessageClick}>Nhn tin</button>
                        </div>

                        {isFriendRequestModalOpen && (
                            <div className="friend-request-modal">
                                <div className="modal-header">
                                    <h2>Gi yu cu kt bn</h2>
                                    <i className="fas fa-times" onClick={() => setIsFriendRequestModalOpen(false)}></i>
                                </div>
                                <div>
                                    <textarea
                                        className="message-sendRequest"
                                        placeholder="Nhp ni dung yu cu kt bn"
                                        value={messageContent}
                                        onChange={(e) => setMessageContent(e.target.value)}
                                    />
                                    <div className="sendRequest-class">
                                        <button className="sendRequest-button" onClick={sendFriendRequest}>Gi yu cu</button>
                                        <button className="closeSendRequest-button" onClick={() => setIsFriendRequestModalOpen(false)}>Hy</button>
                                    </div>
                                </div>
                            </div>
                        )}

                        <div className="personal-info">
                            <p>Gii tnh: {user.gender}</p>
                            <p>Ngy sinh: {formatDate(user.dob)}</p>
                            <p>in thoi: {formatPhoneNumber(user.phoneNumber)}</p>
                        </div>

                        <div className="list-container">
                            <div className="list-item">
                                <i className="fas fa-users"></i>
                                <span>Nhm chung (0)</span>
                            </div>
                            <div className="list-item">
                                <i className="fas fa-id-card"></i>
                                <span>Chia s danh thip</span>
                            </div>
                            <div className="list-item">
                                <i className="fas fa-ban"></i>
                                <span>Chn tin nhn v cuc gi</span>
                            </div>
                            <div className="list-item">
                                <i className="fas fa-exclamation-triangle"></i>
                                <span>Bo xu</span>
                            </div>
                            <div
                                className={`list-item ${friendIds.includes(user.id) ? "" : "disabled"}`}
                                style={{
                                    color: friendIds.includes(user.id) ? "black" : "gray",
                                    cursor: friendIds.includes(user.id) ? "pointer" : "not-allowed",
                                    opacity: friendIds.includes(user.id) ? 1 : 0.5,
                                    pointerEvents: friendIds.includes(user.id) ? "auto" : "none"
                                }}
                            >
                                <i className="fas fa-trash-alt me-2"></i>
                                <span>Xa khi danh sch bn b</span>
                            </div>

                        </div>
                    </div>
                </div>
            </div>
        </div>
    );
};

export default FriendInfoModal;
