import React, { useState, useEffect, useRef } from "react";
import { useWebSocket } from "../context/WebSocket"; // Hook  s dng WebSocket
import "../css/video-call.css";

// Class qun l cuc gi video
class VideoCall {
    constructor(userId, sendMessage) {
        this.userId = userId;
        this.sendMessage = sendMessage; // Truyn sendMessage vo constructor
        this.localStream = null;
        this.peerConnection = null;
        this.remoteStream = null;
        this.isCalling = false;
    }

    // Hm bt u cuc gi
    async startCall(remoteUserId) {
        if (this.isCalling) return;
        this.isCalling = true;

        try {
            this.localStream = await navigator.mediaDevices.getUserMedia({ video: true, audio: true });
            this.displayLocalStream(); // Hin th local stream
        } catch (err) {
            console.error("Error accessing media devices:", err);
            return;
        }

        this.peerConnection = new RTCPeerConnection();

        // Thm local stream vo peer connection
        this.localStream.getTracks().forEach(track => this.peerConnection.addTrack(track, this.localStream));

        // Khi nhn c stream t remote peer
        this.peerConnection.ontrack = (event) => {
            if (event.streams && event.streams[0]) {
                this.remoteStream = event.streams[0];
                this.displayRemoteStream();
            }
        };

        this.peerConnection.onicecandidate = (event) => {
            if (event.candidate) {
                this.sendMessage({
                    type: "candidate",
                    candidate: event.candidate,
                    to: remoteUserId,
                });
            }
        };

        // To offer v gi qua WebSocket
        const offer = await this.peerConnection.createOffer();
        await this.peerConnection.setLocalDescription(offer);

        this.sendMessage({
            type: "offer",
            offer: offer,
            to: remoteUserId,
        });
    }
    // video ngi gi
    displayLocalStream() {
        const localVideoElement = document.getElementById("local-video");
        if (localVideoElement && this.localStream) {
            localVideoElement.srcObject = this.localStream;
        }
    }

    // Hin th remote stream (video ca ngi nhn)
    displayRemoteStream() {
        const remoteVideoElement = document.getElementById("remote-video");
        if (remoteVideoElement && this.remoteStream) {
            remoteVideoElement.srcObject = this.remoteStream;
        }
    }

    // Kt thc cuc gi
    endCall() {
        if (this.peerConnection) {
            this.peerConnection.close();
            this.peerConnection = null;
        }
        if (this.localStream) {
            this.localStream.getTracks().forEach(track => track.stop());
            this.localStream = null;
        }
        this.isCalling = false;
    }
}
const VideoCallComponent = ({ remoteUserId, userId, isVideoCallVisible, setIsVideoCallVisible }) => {
    const { sendMessage } = useWebSocket(); // Ly sendMessage t WebSocket context
    const videoCallRef = useRef(null); // S dng useRef  trnh re-render khng cn thit
    const [isCalling, setIsCalling] = useState(false); // State  qun l trng thi gi

    // Khi to VideoCall ch mt ln khi component mount
    useEffect(() => {
        videoCallRef.current = new VideoCall(userId, sendMessage); // Khi to i tng VideoCall
    }, [userId, sendMessage]);

    const startCall = () => {
        if (videoCallRef.current) {
            videoCallRef.current.startCall(remoteUserId);
            setIsCalling(true);
        }
    };

    const endCall = () => {
        if (videoCallRef.current) {
            videoCallRef.current.endCall();
            setIsCalling(false);
        }
    };

    const modalRef = useRef(null);

    // ng modal khi click ra ngoi
    const handleClickOutside = (event) => {
        if (modalRef.current && !modalRef.current.contains(event.target)) {
            if (videoCallRef.current) {
                videoCallRef.current.endCall(); // Kt thc cuc gi
            }
            setIsCalling(false); // Cp nht trng thi gi
            setIsVideoCallVisible(false); // ng modal
        }
    };

    // Dng useEffect  lng nghe s kin click ngoi
    useEffect(() => {
        if (isVideoCallVisible) {
            document.addEventListener('mousedown', handleClickOutside);
        }

        // Dn dp khi modal khng cn m hoc khi component unmount
        return () => {
            document.removeEventListener('mousedown', handleClickOutside);
        };
    }, [isVideoCallVisible]);

    if (!isVideoCallVisible) return null;

    return (
        <div className="video-call-modal-container" ref={modalRef}>
            <div className="video-call-modal-header">
                <h2>Cuc gi video</h2>
            </div>
            <div className="video-call-modal-buttons">
                <button onClick={startCall} disabled={isCalling}>Bt u gi video</button>
                <button onClick={endCall} disabled={!isCalling}>Kt thc cuc gi</button>
            </div>
            <div className="video-call-modal-streams">
                <video id="local-video" autoPlay muted></video>
                <video id="remote-video" autoPlay></video>
            </div>
        </div>
    );
};

export default VideoCallComponent;