import axios from "axios";

const API_BASE_URL = "https://deploybe-cnm-production.up.railway.app/user"; // a ch API

const UserService = {
  post: async (url, data) => {
    try {
      const response = await axios.post(`${API_BASE_URL}${url}`, data);
      return response.data;
    } catch (error) {
      throw error.response ? error.response.data : error;
    }
  },

  get: async (url, params) => {
    try {
      const response = await axios.get(`${API_BASE_URL}${url}`, { params });
      return response.data;
    } catch (error) {
      throw error.response ? error.response.data : error;
    }
  },

  /**
   * Ly danh sch bn b ca mt user da trn userId
   * @param {string} userId - ID ca user
   * @returns {Promise} - Danh sch bn b
   */
  getFriends: async (userId) => {
    try {
      const response = await axios.get(`${API_BASE_URL}/${userId}/friends`);
      return response.data;
    } catch (error) {
      throw error.response ? error.response.data : error;
    }
  },

  searchUserByName: async (name, userId) => {
    try {
      const response = await axios.get(`${API_BASE_URL}/searchUserByName`, {
        params: { name, userId }, // Gi userId  lc danh sch bn b
      });
      return response.data;
    } catch (error) {
      throw error.response ? error.response.data : error;
    }
  },
  //ly trng thi online offline

  getUserById: async (userId) => {
    try {
      const response = await axios.get(`${API_BASE_URL}/findById/${userId}`);
      return response.data;
    } catch (error) {
      throw error.response ? error.response.data : error;
    }
  },


  /** 
   * API cp nht thng tin ngi dng 
   * @param {string} userId - ID ngi dng
   * @param {Object} data - Thng tin cp nht (name, dob)
   * @returns {Promise<Object>} - Kt qu cp nht
   */
  updateUserInfo: async (userId, data) => {
    try {
      const response = await axios.put(`${API_BASE_URL}/update/${userId}`, data);
      return response.data;
    } catch (error) {
      throw error.response ? error.response.data : error;
    }
  },

  delete: async (url) => {
    try {
      const response = await axios.delete(`${API_BASE_URL}${url}`);
      return response.data;
    } catch (error) {
      throw error.response ? error.response.data : error;
    }
  },
  findByPhoneNumber: async (phone) => {
    try {
      const response = await axios.post(`${API_BASE_URL}/findByPhoneNumber`, {
        phoneNumber: phone
      });
      return response.data;
    } catch (error) {
      throw error.response ? error.response.data : error;
    }
  },
};


export default UserService;
